import { useState } from "react";
import firebase from "...../../firebase";
import { MovieCreateType } from "src/modules/admin/movies";
import { v4 as uuidv4 } from "uuid";

export function AdminCreate() {
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [category, setCategory] = useState("");
  const [image, setImage] = useState("");
  const [nation, setNation] = useState("");
  const [video, setVideo] = useState("");
  const [quality, setQuality] = useState("");
  const [trailer, setTrailer] = useState("");
  const [year, setYear] = useState("");
  const [director, setDirector] = useState("");
  const [slug, setSlug] = useState("");

  const ref = firebase.firestore().collection("movies");

  // ADD function

  function addMovie(newMovie: MovieCreateType) {
    setName("");
    setDescription("");
    setNation("");
    setQuality("");
    setCategory("");
    setSlug("");
    setDirector("");
    setYear("");
    setImage("");
    setVideo("");
    setTrailer("");
    ref
      .doc(newMovie.id as any)
      .set(newMovie)
      .catch((err) => {
        console.error(err);
      });
  }
  return (
    <div className="w-6/12 ml-60 mt-6">
      <h1 className="flex justify-center mb-7 text-4xl text-indigo-500 font-medium">
        Add Movie Here
      </h1>
      <div className="flex flex-col text-black ">
        <div className="flex">
          <h2 className="text-white w-24">Name: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie name here..."
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
        </div>
        <div className="flex ">
          <label className="text-white w-24">Category: </label>
          <select
            className="flex flex-1 mb-6 pl-4 ml-4"
            onChange={(e) => setCategory(e.target.value)}
          >
            <option value="Action Movie">Action Movie</option>
            <option value="Romantic Movie">Romantic Movie</option>
            <option value="Cartoon Movie">Cartoon Movie</option>
            <option value="Costume Dramas">Costume Dramas Movie</option>
          </select>
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Description: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie description here..."
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Quality: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie description here..."
            value={quality}
            onChange={(e) => setQuality(e.target.value)}
          />
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Year: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie description here..."
            value={year}
            onChange={(e) => setYear(e.target.value)}
          />
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Image: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie Image URL here..."
            value={image}
            onChange={(e) => setImage(e.target.value)}
          />
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Nation: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie nation here..."
            value={nation}
            onChange={(e) => setNation(e.target.value)}
          />
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Video: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie video ID here..."
            value={video}
            onChange={(e) => setVideo(e.target.value)}
          />
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Trailer: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie video ID here..."
            value={trailer}
            onChange={(e) => setTrailer(e.target.value)}
          />
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Director: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter movie director here..."
            value={director}
            onChange={(e) => setDirector(e.target.value)}
          />
        </div>
        <div className="flex ">
          <h2 className="text-white w-24">Slug: </h2>
          <input
            className="flex flex-1 mb-6 pl-4 ml-4"
            type="text"
            placeholder="Enter slug here..."
            value={slug}
            onChange={(e) => setSlug(e.target.value)}
          />
        </div>
        <button
          className="px-4 bg-blue-500 rounded text-white w-20 py-1 m-auto"
          onClick={() =>
            addMovie({
              description,
              name,
              category,
              image,
              nation,
              video,
              trailer,
              director,
              quality,
              slug,
              year,
              id: uuidv4(),
            } as MovieCreateType)
          }
        >
          Submit
        </button>
      </div>
    </div>
  );
}
