import { useState, useEffect } from "react";
import firebase from "...../../firebase";
import { MovieType } from "src/modules/admin/movies";
import { DeleteIcon } from "src/components";

export function AdminList() {
  const [movies, setMovies] = useState<MovieType[]>([]);
  const [loading, setLoading] = useState(false);

  const ref = firebase.firestore().collection("movies");

  // Realtime Get data function
  function getMovies() {
    setLoading(true);
    ref.onSnapshot((querySnapshot) => {
      const items: MovieType[] = [];
      querySnapshot.forEach((doc) => {
        items.push(doc.data() as MovieType);
      });
      setMovies(items);
      setLoading(false);
    });
  }
  useEffect(() => {
    getMovies();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  if (loading) {
    return <h1 className="text-white text-xl">Loading... </h1>;
  }

  // Delete function

  function deleteMovie(movie: MovieType) {
    ref
      .doc(movie.id as any)
      .delete()
      .catch((err) => {
        console.error(err);
      });
  }
  return (
    <div className="w-full text-white">
      <div>
        {loading ? <h1>Loading...</h1> : null}
        <h1 className="flex justify-center text-4xl mb-5 text-indigo-500 font-medium">
          List Movie
        </h1>
        <div className="flex justify-between mx-8 capitalize font-medium text-lg mb-2">
          <p className="w-3/12 text-3xl">name movie</p>
          <p className="w-3/12 text-3xl">caterory</p>
          <p className="w-3/12 text-3xl">director</p>
          <p className="text-3xl"> delete</p>
        </div>
        {movies.map((movie) => (
          <div className="flex justify-around text-lg mb-2 ">
            <p className="w-3/12 capitalize">{movie.name}</p>
            <p className="w-3/12">{movie.category}</p>
            <p className="w-3/12">{movie.director}</p>
            <div>
              <button
                className=" mr-5 text-2xl hover:scale-105"
                onClick={() => deleteMovie(movie)}
              >
                <DeleteIcon />
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
