import { useState, useEffect } from "react";
import firebase from "...../../firebase";
import { MovieType } from "src/modules/admin/movies";

export function AdminEditMovie() {
  const [movies, setMovies] = useState<MovieType[]>([]);
  const [loading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [category, setCategory] = useState("");
  const [image, setImage] = useState("");
  const [nation, setNation] = useState("");
  const [video, setVideo] = useState("");
  const [quality, setQuality] = useState("");
  const [director, setDirector] = useState("");
  const [slug, setSlug] = useState("");

  const ref = firebase.firestore().collection("movies");

  // Realtime Get data function
  function getMovies() {
    setLoading(true);
    ref.onSnapshot((querySnapshot) => {
      const items: MovieType[] = [];
      querySnapshot.forEach((doc) => {
        items.push(doc.data() as MovieType);
      });
      setMovies(items);
      setLoading(false);
    });
  }
  useEffect(() => {
    getMovies();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  if (loading) {
    return <h1 className="text-white text-xl">Loading... </h1>;
  }
  // Edit funtion
  function editMovie(updateMovie: MovieType) {
    ref
      .doc(updateMovie.id as any)
      .update(updateMovie)
      .catch((err) => {
        console.error(err);
      });
  }
  return (
    <div className="flex w-full ">
      <div className="w-6/12 text-white">
        {loading ? <h1>Loading...</h1> : null}
        <h1 className="flex justify-center text-4xl mb-5 mt-6 text-indigo-500 font-medium">
          List Movie
        </h1>
        <div className="flex justify-between mx-8 capitalize font-medium text-lg mb-2">
          <p className="ml-10 w-5/12 text-3xl">name movie</p>
          <p className="text-3xl mr-10"> edit</p>
        </div>
        {movies.map((movie) => (
          <div className="flex justify-around text-lg mb-2">
            <p className="w-5/12 capitalize">{movie.name}</p>
            <div>
              <button
                className="px-4 bg-blue-500 rounded"
                onClick={() =>
                  editMovie({
                    description,
                    name,
                    quality,
                    category,
                    image,
                    nation,
                    video,
                    director,
                    slug,
                    id: movie.id,
                  } as MovieType)
                }
              >
                Edit
              </button>
            </div>
          </div>
        ))}
      </div>
      <div className="w-5/12 ml-10 mt-6">
        <h1 className="flex justify-center mb-7 text-4xl text-indigo-500 font-medium">
          Add Movie Here
        </h1>
        <div className="flex flex-col text-black ">
          <div className="flex">
            <h2 className="text-white w-24">Name: </h2>
            <input
              className="flex flex-1 mb-6 pl-4 ml-4"
              type="text"
              placeholder="Enter movie name here..."
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </div>
          <div className="flex ">
            <label className="text-white w-24">Category: </label>
            <select
              className="flex flex-1 mb-6 pl-4 ml-4"
              onChange={(e) => setCategory(e.target.value)}
            >
              <option value="Action Movie">Action Movie</option>
              <option value="Romantic Movie">Romantic Movie</option>
              <option value="Cartoon Movie">Cartoon Movie</option>
              <option value="Costume Dramas">Costume Dramas Movie</option>
            </select>
          </div>
          <div className="flex ">
            <h2 className="text-white w-24">Description: </h2>
            <input
              className="flex flex-1 mb-6 pl-4 ml-4"
              type="text"
              placeholder="Enter movie description here..."
              value={description}
              onChange={(e) => setDescription(e.target.value)}
            />
          </div>
          <div className="flex ">
            <h2 className="text-white w-24">Image: </h2>
            <input
              className="flex flex-1 mb-6 pl-4 ml-4"
              type="text"
              placeholder="Enter movie Image URL here..."
              value={image}
              onChange={(e) => setImage(e.target.value)}
            />
          </div>
          <div className="flex ">
            <h2 className="text-white w-24">Quality: </h2>
            <input
              className="flex flex-1 mb-6 pl-4 ml-4"
              type="text"
              placeholder="Enter movie Image URL here..."
              value={quality}
              onChange={(e) => setQuality(e.target.value)}
            />
          </div>
          <div className="flex ">
            <h2 className="text-white w-24">Nation: </h2>
            <input
              className="flex flex-1 mb-6 pl-4 ml-4"
              type="text"
              placeholder="Enter movie nation here..."
              value={nation}
              onChange={(e) => setNation(e.target.value)}
            />
          </div>
          <div className="flex ">
            <h2 className="text-white w-24">Video: </h2>
            <input
              className="flex flex-1 mb-6 pl-4 ml-4"
              type="text"
              placeholder="Enter movie video ID here..."
              value={video}
              onChange={(e) => setVideo(e.target.value)}
            />
          </div>
          <div className="flex ">
            <h2 className="text-white w-24">Director: </h2>
            <input
              className="flex flex-1 mb-6 pl-4 ml-4"
              type="text"
              placeholder="Enter movie director here..."
              value={director}
              onChange={(e) => setDirector(e.target.value)}
            />
          </div>
          <div className="flex ">
            <h2 className="text-white w-24">Slug: </h2>
            <input
              className="flex flex-1 mb-6 pl-4 ml-4"
              type="text"
              placeholder="Enter slug here..."
              value={slug}
              onChange={(e) => setSlug(e.target.value)}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
