import { Link } from "react-router-dom";
import { HomeIcon, AvatarIcon, ListIcon, CreateIcon, EditIcon } from "src/components";
export function AdminSideBar() {
  return (
    <div className="w-2/12 h-auto -mb-20 text-white bg-blue-900 pl-6 pt-6">
      <div className="flex mb-3">
        <HomeIcon />
        <h1 className="ml-2 xl:text-xl lg:text-xl md:text-lg sm:text-sm">Admin</h1>
      </div>
      <div className="flex mb-3">
        <AvatarIcon />
        <p className="ml-2 xl:text-xl lg:text-xl md:text-lg sm:text-sm">Administrator</p>
      </div>
      <Link to="/admin/admin-list">
        <div className="flex mb-3">
          <ListIcon />
          <h1 className="ml-2 xl:text-xl lg:text-xl md:text-lg sm:text-sm">List Movies</h1>
        </div>
      </Link>
      <Link to="/admin/admin-create">
        <div className="flex mb-3">
          <CreateIcon />
          <h1 className="ml-2 xl:text-xl lg:text-xl md:text-lg sm:text-sm">Create Movie</h1>
        </div>
      </Link>
      <Link to="/admin/admin-edit">
        <div className="flex mb-3">
          <EditIcon />
          <h1 className="ml-2 xl:text-xl lg:text-xl md:text-lg sm:text-sm">Edit Movie</h1>
        </div>
      </Link>
    </div>
  );
}
